	module.exports = {"data":{"post":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/cities-of-mine/index_ru.md absPath of file >>> MarkdownRemark","html":"<p>Очень прикольно наблюдать за тем как изменяется осознание города. С Москвой тоже так было, но я не наблюдала за процессом. Просто постепенно город превращался из группы обитаемых оазисов вокруг знакомых станций метро в нечто цельное, со своими ориентирами, подкрепленными воспоминаниями.\n<excerpt/></p>\n<p>Теперь я уже специально наблюдаю. Вчера была в том районе, где мы жили первый месяц, но довелось пройти немного другим маршрутом и внезапно “карта” района начала складываться в голове. Только я не имею в виду расположение улиц и прочую геометрию, а то, чем район живет: «Вот тут у них, оказывается, ресторанчик. А здесь кружок бисероплетения для всяких бабушек. И почему мы не ходили в эту сторону раньше?»</p>\n<p>Когда дело было закончено, в карте ассоциаций появилась ещё одна запись. Я побывала в районной библиотеке. И хотя раньше я ходила мимо того дома, но не замечала его, он был безликим. А теперь это даже не просто библиотека, а <em>та самая</em> библиотека. Домик видно со многих других точек района. Каждая новая ассоциация не просто появится в карте, а будет связана с этой.</p>\n<p>В Хельсинки этот процесс особенно интересен. Тут столько всего спрятано! Никогда не догадаешься, что, оказывается, этот квартал можно пройти под землей. А на 8м этаже вот этого безликого здания — ресторан с бизнесланчем (не-а, вывески там нет).</p>\n<p>Прошло всего пару месяцев, а город уже немножко мой.</p>","fields":{"slug":"ru/life/cities-of-mine/","prefix":"","disqusIdentifier":"life-cities-of-mine-index-ru"},"frontmatter":{"title":"«Мои» города","date":"15 October 2014","v2":true,"old":null,"tumblr":true,"meta":null,"cover":null}},"authornote":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Mr. Gatsby</strong> Proin ornare ligula eu tellus tempus elementum. Aenean <a href=\"/\">bibendum</a> iaculis mi, nec blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus. Blandit lacus interdum vitae. Vestibulum non nibh risus, a scelerisque purus.</p>"},"site":{"siteMetadata":{"siteUrl":"http://varya.me/"}}},"pathContext":{"slug":"ru/life/cities-of-mine/","prev":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/documents-for-dutch-visa-apostille/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/documents-for-dutch-visa-apostille/","prefix":"","lang":"ru","disqusIdentifier":"life-documents-for-dutch-visa-apostille-index-ru"},"frontmatter":{"title":"Документы для оформления визы в Голландию (Апостиль)"}},"next":{"id":"/Users/varya/WebDev/Personal/varya.github.com/content/life/christmas-2013-in-dusseldorf/index_ru.md absPath of file >>> MarkdownRemark","fields":{"slug":"ru/life/christmas-2013-in-dusseldorf/","prefix":"","lang":"ru","disqusIdentifier":"life-christmas-2013-in-dusseldorf-index-ru"},"frontmatter":{"title":"Рождество 2013 в Дюссельдорфе"}}}}

